// Fondo
.bg_animate {
  height: 100vh;
  width: 100%;
  max-width: 100%;
  max-height: 100vh;
  background: $fondo;
  position: relative;
}
// Fin fondo

// Inicio barra y menu
.bar {
  width: 20%;
  margin-top: 2rem;
  cursor: pointer;
}
.barra {
  width: 6rem;
  height: 6rem;
  cursor: pointer;
  a img {
    fill:invert(100%);
  }
  position: absolute;
  right: 5rem;
  
}
.main {
  visibility: hidden;
  padding-top: 2rem;
  margin: 0 auto;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  right: -10%;
  @include tablet {
    right: -5%;
  }
  h2 {
    font-size: 1.5rem;
    max-height: 1rem;
    margin: 0 auto;
    transition: 0.5s color;
  }
  nav {
    position: relative;
    display: grid;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-items: center;
    margin: 0 auto;
    grid-template-rows: 50% 50%;
    a {
      justify-content: center;
      line-height: 1rem;
      font-family: $fuente_principal;
      align-items: center;
      text-align: center;
      color: white;
    }
  }
}
// Final barra y menu

// animacion letras
.ballons {
  position: absolute;
  top: 5rem;
  z-index: 0;
  width: 100%;
  @include grid(17, 0);
  .ballon h3 {
    font-size: 1.1rem;
  }
  .ballon h4 {
    font-size: 1rem;
  }
  height: 1;
}
.ballon {
  h4, h3{
    font-family: $fuente_principal;
  }
  display: inline;
  opacity: 0;
  color: $amarillo;
  position: relative;
  animation: ballons 7s linear infinite;
  &:nth-child(2n) {
    animation-delay: 1s;
  }
  &:nth-child(2n-1) {
    animation-delay: 2s;
  }
  &:nth-child(3n) {
    animation-delay: 3s;
  }
  &:nth-child(6n) {
    animation-delay: 3.5s;
  }
  &:nth-child(4n) {
    animation-delay: 2.5s;
  }

}
// Final animaciones letras

// Coleres letras
.disponible {
  color: $amarillo;
}
.lenguaje {
  color:$letras;
  font-weight: bold;
  opacity: 1;
}
.deg-90 {
  transform: rotate(90deg);
  letter-spacing: 1rem;
}
// Fin colores letras

// Inicio activar visibilidad
.menu {
  background-color: black;
  .ballons {
    display: none;
  }
  .barra {
    width: 100%;
    height: 100vh;

    a img {
      width: 5rem;
      position: absolute;
      right: 0;
      top: 0;
      filter: invert(100%);
    }
    .main {
      width: 100%;
    }
  }
  .main {
    visibility: visible;
  }
  .contenedor-resumen {
    display: none;
  }
}

// Fin activa vicibilidad

// Clase letras enormes

// Animaciones
@keyframes esconder {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes ballons {
  0% {
    top: 0;
    opacity: 0;
  }
  30% {
    transform: translateX(1rem);
  }
  50% {
    top: 30vh;
    opacity: 1;
  }
  

  100% {
    top: 100vh;
    opacity: 0;
  }
}


@keyframes movimiento {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(1rem);
  }
  100% {
    transform: translateY(0);
  }
}
