/** Media Queries **/
@mixin mobil {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 100% / $columnas);
}

@mixin boton($color-fondo, $display:block, $color-letra:$fondo) {
    background-color: $color-fondo;
    color: $color-letra;
    padding: 1rem;
    text-align: center;
    margin-top: $separacion / 2;
    display: $display;
    border: none;
    &:hover {
        background-color: darken($color-fondo, 5);
        cursor: pointer;
    }
    border-radius: 1rem;
}