body {
  font-family: "Nova Mono", monospace;
  font-size: 1.6rem;
  line-height: 1.8;
  max-height: 100vh;
  overflow: hidden;
  width: 100%; }

p {
  color: black;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  text-transform: uppercase;
  margin: 2.5rem 0;
  font-weight: 500;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

li {
  list-style: none; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 80rem; }

.alinear-derecha {
  display: flex;
  justify-content: flex-end; }

.dark-mode {
  background-color: #262626; }
  .dark-mode p {
    color: white; }
  .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
    color: #e1e1e1; }
  .dark-mode blockquote {
    color: white; }
  .dark-mode .anuncio {
    background-color: #333333;
    border: 1px solid #333333; }
  .dark-mode .iconos-caracteristicas img {
    filter: invert(100%); }
  .dark-mode .precio {
    color: green; }
  .dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
    color: #e1e1e1; }
  .dark-mode .formulario input:not([type="submit"]), .dark-mode .formulario textarea, .dark-mode .formulario select {
    padding: 2rem;
    display: block;
    width: 100%;
    background-color: black;
    border: 1px solid #e1e1e1;
    margin-bottom: 2rem;
    color: #e1e1e1; }

.bg_animate {
  height: 100vh;
  width: 100%;
  max-width: 100%;
  max-height: 100vh;
  background: #353535;
  position: relative; }

.bar {
  width: 20%;
  margin-top: 2rem;
  cursor: pointer; }

.barra {
  width: 6rem;
  height: 6rem;
  cursor: pointer;
  position: absolute;
  right: 5rem; }
  .barra a img {
    fill: invert(100%); }

.main {
  visibility: hidden;
  padding-top: 2rem;
  margin: 0 auto;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  right: -10%; }
  @media (min-width: 85rem) {
    .main {
      right: -5%; } }
  .main h2 {
    font-size: 1.5rem;
    max-height: 1rem;
    margin: 0 auto;
    transition: 0.5s color; }
  .main nav {
    position: relative;
    display: grid;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-items: center;
    margin: 0 auto;
    grid-template-rows: 50% 50%; }
    .main nav a {
      justify-content: center;
      line-height: 1rem;
      font-family: "Nova Mono", monospace;
      align-items: center;
      text-align: center;
      color: white; }

.ballons {
  position: absolute;
  top: 5rem;
  z-index: 0;
  width: 100%;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(17, 5.88235%);
  height: 1; }
  .ballons .ballon h3 {
    font-size: 1.1rem; }
  .ballons .ballon h4 {
    font-size: 1rem; }

.ballon {
  display: inline;
  opacity: 0;
  color: #F5BB00;
  position: relative;
  animation: ballons 7s linear infinite; }
  .ballon h4, .ballon h3 {
    font-family: "Nova Mono", monospace; }
  .ballon:nth-child(2n) {
    animation-delay: 1s; }
  .ballon:nth-child(2n-1) {
    animation-delay: 2s; }
  .ballon:nth-child(3n) {
    animation-delay: 3s; }
  .ballon:nth-child(6n) {
    animation-delay: 3.5s; }
  .ballon:nth-child(4n) {
    animation-delay: 2.5s; }

.disponible {
  color: #F5BB00; }

.lenguaje {
  color: #528BFF;
  font-weight: bold;
  opacity: 1; }

.deg-90 {
  transform: rotate(90deg);
  letter-spacing: 1rem; }

.menu {
  background-color: black; }
  .menu .ballons {
    display: none; }
  .menu .barra {
    width: 100%;
    height: 100vh; }
    .menu .barra a img {
      width: 5rem;
      position: absolute;
      right: 0;
      top: 0;
      filter: invert(100%); }
    .menu .barra .main {
      width: 100%; }
  .menu .main {
    visibility: visible; }
  .menu .contenedor-resumen {
    display: none; }

@keyframes esconder {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes ballons {
  0% {
    top: 0;
    opacity: 0; }
  30% {
    transform: translateX(1rem); }
  50% {
    top: 30vh;
    opacity: 1; }
  100% {
    top: 100vh;
    opacity: 0; } }

@keyframes movimiento {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(1rem); }
  100% {
    transform: translateY(0); } }

.JHON-ROBERT, .PROGRAMER,
.NOW, .AVAILABLE, .FOR-YOU, .WORKS {
  visibility: visible;
  transition: 1s visibility;
  color: #F5BB00; }
  @media (min-width: 85rem) {
    .JHON-ROBERT, .PROGRAMER,
    .NOW, .AVAILABLE, .FOR-YOU, .WORKS {
      visibility: hidden; } }

.visible {
  visibility: visible; }

.ocultar {
  visibility: hidden; }

.contenedor-resumen {
  display: block;
  margin: 0 auto;
  align-items: center;
  height: 50vh;
  width: 100%;
  text-align: center;
  width: 100%;
  height: 10rem;
  visibility: none;
  display: flex; }
  .contenedor-resumen h2 {
    width: 100%;
    margin-top: 70vh;
    font-size: 2rem; }
  @media (min-width: 85rem) {
    .contenedor-resumen {
      padding: 10rem 5rem; } }

a {
  font-family: "Nova Mono", monospace;
  color: #528BFF; }

.view-work {
  opacity: 1;
  width: 30%;
  height: 10rem;
  font-size: 1rem;
  padding: 1rem;
  border-radius: 1rem;
  border: 1px solid grey; }
  .view-work:hover {
    border: none; }

.PROGRAMER {
  font-family: 'Nova Mono';
  position: relative;
  text-shadow: none;
  font-size: 2rem;
  transition: all 2s; }

.colores {
  color: white;
  animation: colores 3.5s 2;
  text-shadow: 0 0 5px #00b2ff, 0 0 10px #00b2ff, 0 0 20px #00b2ff, 0 0 40px #26687f, 0 0 80px #26687f; }

@keyframes colores {
  0% {
    text-shadow: none; }
  33% {
    text-shadow: 0 0 5px #00b2ff, 0 0 10px #00b2ff, 0 0 20px #00b2ff, 0 0 40px #26687f, 0 0 80px #26687f; }
  66% {
    text-shadow: none; }
  100% {
    text-shadow: 0 0 5px #00b2ff, 0 0 10px #00b2ff, 0 0 20px #00b2ff, 0 0 40px #26687f, 0 0 80px #26687f; } }
