.PROGRAMER{
  font-family: 'Nova Mono';
  position: relative;
  text-shadow: none;
  font-size: 2rem;
  transition: all 2s;
}
.colores {
  color: white;
  animation: colores 3.5s 2;
  text-shadow:
  0 0 5px rgba(0,178,255,1),
  0 0 10px rgba(0,178,255,1),
  0 0 20px rgba(0,178,255,1),
  0 0 40px rgba(38,104,127,1),
  0 0 80px rgba(38,104,127,1);
}

@keyframes colores {
  0% {
    text-shadow: none;
  }
  33% {
    text-shadow:
  0 0 5px rgba(0,178,255,1),
  0 0 10px rgba(0,178,255,1),
  0 0 20px rgba(0,178,255,1),
  0 0 40px rgba(38,104,127,1),
  0 0 80px rgba(38,104,127,1);
  }
  66% {
    text-shadow: none;
  }
  100% {
    text-shadow:
  0 0 5px rgba(0,178,255,1),
  0 0 10px rgba(0,178,255,1),
  0 0 20px rgba(0,178,255,1),
  0 0 40px rgba(38,104,127,1),
  0 0 80px rgba(38,104,127,1);
  }
}

