
.JHON-ROBERT, .PROGRAMER,
.NOW, .AVAILABLE, .FOR-YOU, .WORKS{

  visibility: visible;
  @include tablet {
    visibility: hidden;
  }
  transition: 1s visibility;
  color: $amarillo;
}
.visible {
  visibility: visible;
}
.ocultar {
  visibility: hidden;
}
.contenedor-resumen {
  display: block;
  margin: 0 auto;
  align-items: center;
  height: 50vh;
  width: 100%;
  h2 {
    width: 100%;
    margin-top: 70vh;
    font-size: 2rem;
  }
  @include tablet{
    padding: 10rem 5rem;
  }
  text-align: center;
  width: 100%;
  height: 10rem;
  visibility: none;
  display: flex;
}

a {
  font-family: $fuente_principal;
  color: $letras;
}

.view-work {
  opacity: 1;
  width: 30%;
  height: 10rem;
  font-size: 1rem;
  padding: 1rem;
  border-radius: 1rem;
  border: 1px solid grey;
  &:hover {
    border: none;
  }
}