.dark-mode {
    background-color: darken($grisOscuro,5);
    p{
        color: white;
    }
    a, h1, h2, h3, h4 {
        color: $gris;
    }
    blockquote {
        color: white;
    }

    .anuncio {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;        
    }

    .iconos-caracteristicas {
        img {
            filter: invert(100%);
        }
    }
    .precio {
        color: green;//cambiar
    }
    .formulario {
        label, legend, p {
            color: $gris;
        }
        input:not([type="submit"]), textarea, select{
            padding: 2rem;
            display: block;
            width: 100%;
            background-color: black;
            border: 1px solid $gris;
            margin-bottom: 2rem;
            color: $gris;
            
        }
    }
}